* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    list-style: none;
    text-decoration: none;
    box-sizing: border-box;
}
  
  /* THEME CHANGE */
  main.color-1 {
    --primary-hue: 270;
  }
  
  main.color-2 {
    --primary-hue: 110;
  }
  
  main.color-3 {
    --primary-hue: 228;
  }
  
  main.color-4 {
    --primary-hue: 325;
  }
  
  main.color-5 {
    --primary-hue: 0;
  }
  
  main.color-6 {
    --primary-hue: 55;
  }
  
  main.bg-2 {
    --black-lightness: 96%;
    --dark-lightness: 60%;
    --light-lightness: 12%;
    --white-lightness: 6%;
    --color-white: hsl(0, 5%, var(--white-lightness));
    --color-light: hsl(var(--primary-hue), 5%, var(--light-lightness));
    --color-black: hsl(var(--primary-hue), 5%, var(--black-lightness));
    --color-dark: hsl(var(--primary-hue), 5%, var(--dark-lightness));
  }
  
  main {
    color: var(--color-dark);
    --color-primary: hsl(var(--primary-hue), 89%, 41%);
  }
  
  /* GENERAL CONTAINER CLASS */
  .container {
    width: var(--container-width-lg);
    margin-inline: auto;
    max-width: 1920px;
  }
  
  /* GENERAL SECTION STYLES */
  section {
    padding: 10rem 0;
    background: var(--color-light);
    transition: var(--transition);
  }
  
  section:nth-child(even) {
    background: var(--color-white);
  }
  
  section > h2 {
    text-align: center;
  }
  
  section > p {
    text-align: center;
    margin: 0.5rem auto 4rem;
    width: 40%;
  }
  
/* GENERAL SECTION STYLES */
a {
    transition: var(--transition);
}

/* GENERAL HEADING STYLES */
h1,
h2,
h3,
h4,
h5,
h6 {
    line-height: 1.1;
    color: var(--color-black);
}

h1 {
    font-size: 3rem;
}

h2 {
    font-size: 2rem;
}

h3 {
    font-size: 1.6rem;
}

h4 {
    font-size: 1.1rem;
}

h5 {
    font-size: 0.95rem;
}

h6 {
    font-size: 0.8rem;
}

/* GENERAL IMAGES STYLES */
img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/* MEDIA QUERIES (tablets) */
@media screen and (max-width: 1024px) {
    body {
        font-size: 1rem;
    }

    .container {
        width: var(--container-width-md);
    }

    section {
        padding: 8rem 0;
    }

    section > p {
        width: 72%;
        margin-bottom: 3rem;
    }

    h1 {
        font-size: 2rem;
    }
}

/* MEDIA QUERIES (phones) */
@media screen and (max-width: 600px) {
    h1 {
        font-size: 2rem;
    }

    h2 {
        font-size: 1.8rem;
    }

    h3 {
        font-size: 1.4rem;
    }

    h4 {
        font-size: 1rem;
    }

    .btn {
        padding: 0.6rem 1.5rem;
    }

    section > p {
        width: var(--container-width-md);
    }
}